
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.row.column{
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.cell{
  -webkit-box-flex: 1;
      -ms-flex: 1 auto;
          flex: 1 auto;
}
.cell.basis100{
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
}
.cell.nogrow{
  -webkit-box-flex: 0;
      -ms-flex-positive: 0;
          flex-grow: 0;
}
.cell.scroll{
  overflow: auto;
}
.mc-main {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.mc-left,
.mc-right {
  height: 100%;
}
.mc-left {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 200px;
          flex: 0 1 200px;
}
.mc-right {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 100%;
          flex: 1 1 100%;
  overflow: hidden;
}
.mc-nav-wrapper {
  min-height: 100%;
}
.mc-nav {
  height: 100%;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: .25s;
          transition-duration: .25s;
}
.fade-enter-active {
  -webkit-transition-delay: .25s;
          transition-delay: .25s;
}
.fade-enter, .fade-leave-active {
  opacity: 0
}



